import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class EmailService {
  // PRODUCTION LINK: private baseUrl = 'https://tonyvu.dev';
  // DEBUG LINK: private baseUrl = "http://localhost:4200";
  // private baseUrl = "https://tonyvu.dev/message";
  private baseUrl = "http://localhost:5001/message";

  constructor(private http: HttpClient) {}

  sendEmail(name: string, email: string, subject: string, messageBody: string){
    const headers = { "Content-Type": "application/json" };
    const body = {
      name: name,
      email: email,
      subject: subject,
      messageBody: messageBody
    };

    return new Promise((resolve, reject) => {
      this.http
        .post<any>(this.baseUrl, body, { headers })
        .subscribe(
          (res: Response) => {
            resolve();
          },
          (error) => {
            reject();
          }
        );
    });
  }
}
